import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'

const start = keyframes`
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
`;

const Start = styled.div`
  opacity: 0;
  animation: ${start} 3s 0.2s cubic-bezier(0.2, 0.8, 0.2, 1);
  animation-fill-mode: forwards;
`;

const rainbow = keyframes`
  0% {

        color: #ffe0e0;
      }

      10% {

        color: #ffe1ac;
      }

      20% {

        color: #fffaa8;
      }

      30% {

        color: #bffaa8;
      }

      40% {

        color: #bff3e0;
      }

      50% {

        color: #9fe0f8;
      }

      60% {

        color: #9fbaed;
      }

      70% {

        color: #ad99d8;
      }
      80% {

        color: #9f7af8;
      }

      90% {

        color: #c48bf6;
      }

      100% {

        color: #e6b1f6;
      }
`;

const StyledText = styled.span`
  font-family: 'Snell Roundhand', 'Snell Roundhand Script', serif;
  font-weight: bold;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);

  :hover {
    animation: ${rainbow} 20s infinite alternate;
    animation-fill-mode: forwards;
  }
`;

const Content = styled.div`
  z-index: 10;
  height: 100%
  background-size: cover;
  background-position: center;
  cursor: url(../assets/cursor/empty.svg), auto;
  position: float;
`;

const ContentGroup = styled.div`
  margin: 0 auto;
  max-width: 1180px;
  font-size: 100px;
  padding: 50px 0px;

  @media (max-width: 1382px) {
    max-width: 80%
  }

  @media (max-width: 1024px) {
    font-size: 80px;
  }

  @media (max-width: 768px) {
    max-width: 90%
    font-size: 60px;
    padding: 15px 0px;
  }
`;

const Parent = styled.div`
  .Img {
    z-index: -1;
    position: absolute;
    max-width: 60%;
    max-height: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0%;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  }

  .ImgHover {
    opacity: 100%
  }
`;

class IndexPage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <Parent height={this.state.height}>
                <img src={require("../assets/images/jaden.jpg")} className={this.state.hover1 ? "Img ImgHover" : "Img"}/>
                <img src={require("../assets/images/cs.png")} className={this.state.hover2 ? "Img ImgHover" : "Img"}/>
                <img src={require("../assets/images/art.jpg")} className={this.state.hover3 ? "Img ImgHover" : "Img"}/>
                <img src={require("../assets/images/pom.png")} className={this.state.hover4 ? "Img ImgHover" : "Img"}/>
              </Parent>
              <p>
                HI, I'M&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover1')} onMouseOut={() => this.mouseOut('hover1')}>
                Jaden
                </StyledText>.&nbsp;

                I'M A&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover2')} onMouseOut={() => this.mouseOut('hover2')}>
                <Link to='/code/'>
                Computer Science
                </Link>
                </StyledText>&nbsp;

                MAJOR AND&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover3')} onMouseOut={() => this.mouseOut('hover3')}>
                <Link to='/art/'>
                Art
                </Link>
                </StyledText>&nbsp;

                MINOR AT&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover4')} onMouseOut={() => this.mouseOut('hover4')}>
                <a href='https://pomona.edu'>
                Pomona College
                </a>
                </StyledText>.
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default IndexPage



